.box {
  max-width: 1200px;
  margin: 0 auto;
  border: 1px solid red;
  display: flex;
  flex-direction: row;
  background-color: #dadada;
}

.title:hover {
  text-decoration: underline;
}

.genre {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}

.genre li {
  list-style: none;
  padding-right: 20px;
}

.imgWrap {
  width: 30%;
  min-width: 230px;
  text-align: center;
  border: 1px solid black;
}

.textWrap {
  min-width: 30%;
  height: 305px;
  padding: 20px 40px;
  text-align: left;

  text-overflow: ellipsis;
}

.summary {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 8;
  overflow: hidden;
}

.genreBtn {
  padding: 2px 12px;
  background-color: orange;
  color: #ffffff;
  border-radius: 4px;
}
